import { Component, Input, Self, AfterViewInit } from '@angular/core';
import { NgControl } from '@angular/forms';
import { I18nBase } from './base';

@Component({
    template: ''
})


export class I18nOptions extends I18nBase {
    
    @Input() listTmp: any;
    @Input() textPath: string = 'option';
    @Input() valuePath: string = 'value';
    @Input() toTranslate: boolean = true;

    listFinal: any[] = [];
    
    ngOnInit(): void {
        if (this.listTmp) {
            this.showOptions();
        }
    }

    showOptions() {
        this.listFinal = [];
        if( 
            typeof(this.listTmp) === 'string' && 
            this.toTranslate
             && this.textPath == 'option' && 
            this.valuePath == 'value' 
        ){
            this._srvTranslatePipe.get(this.listTmp).subscribe(item => {
                const tmp = Object.keys(item).map(key => item[key]);
                for (const item of tmp) {
                    this.listFinal.push(
                        {
                            option: item,
                            value: item
                        }
                    )
                }
            });    
        }else if(
            typeof(this.listTmp) === 'object' &&
            !this.toTranslate
        ){
            this.listFinal = this.listTmp;
        }else{
            throw Error('Error en la estructura de inputs! VerifiquÃ©lo y vuelva a intentar.');
        }
    }
}
